const baseAPI = 'https://dbprod-f9cb.restdb.io/rest';
// const baseAPI = 'http://localhost:3000';

const productService = {
  get() {
    return new Promise((resolve, reject) => {
      fetch(`${baseAPI}/products`, {
        method: 'GET',
        headers: {
          'cache-control': 'no-cache',
          'x-apikey': '6158c6b18597142da1745317'
        }
      })
        .then(response => response.json())
        .then(json => resolve(json))
        .catch(err => {
          reject(err);
        });
    });
  },

  create(product) {
    return new Promise((resolve, reject) => {
      fetch(`${baseAPI}/products`, {
        method: 'POST',
        body: JSON.stringify(product),
        headers: {
          'cache-control': 'no-cache',
          'x-apikey': '6158c6b18597142da1745317',
          'content-type': 'application/json'
        },
        json: true
      })
        .then(result => result.json())
        .then(json => resolve(json))
        .catch(err => {
          reject(err);
        });
    });
  },

  update(product) {
    return new Promise((resolve, reject) => {
      fetch(`${baseAPI}/products/${product.id}`, {
        method: 'PUT',
        body: JSON.stringify(product),
        headers: {
          'cache-control': 'no-cache',
          'x-apikey': '6158c6b18597142da1745317',
          'content-type': 'application/json'
        },
        json: true
      })
        .then(result => {
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  },

  destroy(product) {
    return new Promise((resolve, reject) => {
      fetch(`${baseAPI}/products/${product.id}`, {
        method: 'DELETE',
        headers: {
          'cache-control': 'no-cache',
          'x-apikey': '6158c6b18597142da1745317',
          'content-type': 'application/json'
        }
      })
        .then(response => response.json())
        .then(json => resolve(json))
        .catch(err => {
          reject(err);
        });
    });
  },

  getCart() {
    return new Promise((resolve, reject) => {
      fetch(`${baseAPI}/cart`, {
        method: 'GET',
        headers: {
          'cache-control': 'no-cache',
          'x-apikey': '6158c6b18597142da1745317'
        }
      })
        .then(response => response.json())
        .then(json => resolve(json))
        .catch(err => {
          reject(err);
        });
    });
  },

  createCart(product) {
    return new Promise((resolve, reject) => {
      fetch(`${baseAPI}/cart`, {
        method: 'POST',
        body: JSON.stringify(product),
        headers: {
          'cache-control': 'no-cache',
          'x-apikey': '6158c6b18597142da1745317',
          'content-type': 'application/json'
        },
        json: true
      })
        .then(result => result.json())
        .then(json => resolve(json))
        .catch(err => {
          reject(err);
        });
    });
  },

  updateCart(product) {
    return new Promise((resolve, reject) => {
      fetch(`${baseAPI}/cart/${product._id}`, {
        method: 'PUT',
        body: JSON.stringify(product),
        headers: {
          'cache-control': 'no-cache',
          'x-apikey': '6158c6b18597142da1745317',
          'content-type': 'application/json'
        },
        json: true
      })
        .then(result => {
          resolve(result);
        })
        .catch(err => {
          reject(err);
        });
    });
  },

  destroyCart(product) {
    return new Promise((resolve, reject) => {
      fetch(`${baseAPI}/cart/${product._id}`, {
        method: 'DELETE',
        headers: {
          'cache-control': 'no-cache',
          'x-apikey': '6158c6b18597142da1745317',
          'content-type': 'application/json'
        }
      })
        .then(response => response.json())
        .then(json => resolve(json))
        .catch(err => {
          reject(err);
        });
    });
  }
};

export default productService;
